.tableInfo {
  border-color: #e6e6e6 !important;
  border-style: solid !important;
  border-width: 1px !important;
}
.table {
  color: #013e87 !important;
}
.tableBody tr:nth-of-type(2n + 1) {
  --bs-table-accent-bg: rgb(1 62 135 / 7%) !important;
  color: #013e87 !important;
}

.darkBlue {
  background-color: #001c43;
}

.bgLightWhite {
  background-color: #e2f0ffcc;
  border-radius: 20px;
  padding: 15px;
}
.btnRight {
  text-align: right !important;
}
.dashBoardBtn {
  color: #fff;
  border: 2px solid #fff !important;
  background-color: #d20001 !important;
  border-radius: 3px !important;
}
.refreshBtn {
  background-color: #d20001;
  margin: 0px;
  text-align: center;
  color: #fff;
  border: none;
  border-radius: 2px;
}
.searchBtn {
  background-color: #001c43;
  right: 50px;
  text-align: center;
  color: #fff;
  border: none;
  border-radius: 2px;
}
.searchBtn:active {
  color: #fff;
  background-color: none;
  border-color: none;
}
.searchBtn:hover:active {
  box-shadow: none;
}
.refreshBtn:hover:active {
  box-shadow: none;
}
